	module.exports = {"data":{"markdownRemark":{"html":"<p>LinkedIn recognizes Free Code Camp as a university.</p>\n<p>You can explore our alumni activity <a href='https://www.linkedin.com/edu/school?id=166029' target='_blank' rel='nofollow'>here</a>.</p>\n<p>You should add Free Code Camp to your LinkedIn profile. Here’s how:</p>\n<ul>\n<li>Follow this link: <a href='https://www.linkedin.com/profile/edit-education?school=Free+Code+Camp' target='_blank' rel='nofollow'>Add Free Code Camp</a></li>\n<li>Follow these instructions:<img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/8/8b138a334f9cfc075fee7379b515a173a154934b.png\"></li>\n</ul>\n<p>Once you complete our curriculum (or complete at least one Nonprofit Project and then get a coding job), we will issue you an official Full Stack Web Development certificate that you can also add to your LinkedIn profile.</p>","fields":{"slug":"/miscellaneous/add-free-code-camp-in-linkedin/"},"frontmatter":{"title":"Add Free Code Camp in Linkedin"}}},"pathContext":{"slug":"/miscellaneous/add-free-code-camp-in-linkedin/"}}

